<template>
  <form @submit.prevent="onSubmit">
    <div class="flex flex-wrap">
      <div class="w-2/4 px-2 my-2">
        <TextField
          name="blackListTable.url"
          labelText="–°—Å—ã–ª–∫–∞ –Ω–∞ —Ç–∞–±–ª–∏—Ü—É —Å —á–µ—Ä–Ω—ã–º —Å–ø–∏—Å–∫–æ–º:"
        />
      </div>
      <div class="w-1/4 px-2 my-2">
        <TextField
          name="blackListTable.sheetName"
          labelText="–ù–∞–∑–≤–∞–Ω–∏–µ –ª–∏—Å—Ç–∞:"
        />
      </div>
      <div class="w-1/4 px-2 my-2">
        <TextField
          name="blackListTable.columnName"
          labelText="–ù–∞–∑–≤–∞–Ω–∏–µ –∫–æ–ª–æ–Ω–∫–∏:"
        />
      </div>

      <div class="w-2/4 px-2 my-2">
        <TextField
          name="checkTable.url"
          labelText="–°—Å—ã–ª–∫–∞ –Ω–∞ –ø—Ä–æ–≤–µ—Ä—è–µ–º—É—é —Ç–∞–±–ª–∏—Ü—É:"
        />
      </div>
      <div class="w-1/4 px-2 my-2">
        <TextField name="checkTable.sheetName" labelText="–ù–∞–∑–≤–∞–Ω–∏–µ –ª–∏—Å—Ç–∞:" />
      </div>
      <div class="w-1/4 px-2 my-2">
        <TextField name="checkTable.columnName" labelText="–ù–∞–∑–≤–∞–Ω–∏–µ –∫–æ–ª–æ–Ω–∫–∏:" />
      </div>

      <div class="w-1/2 px-2 my-2 flex relative">
        <ColorPickerField name="color" />
      </div>

      <div class="w-1/2 px-2 my-2">
        <TextField
          name="interval"
          inputType="number"
          labelText="–ò–Ω—Ç–µ—Ä–≤–∞–ª(–º–∏–Ω):"
        />
      </div>

      <div class="w-full flex my-4">
        <Button center buttonType="submit" :disabled="disabled">
          {{ isSubmitting ? "–ó–∞–≥—Ä—É–∑–∫–∞..." : "–ó–∞–ø—É—Å–∫" }}
        </Button>
      </div>
    </div>
  </form>
</template>

<script>
import { useForm } from "vee-validate";
import * as yup from "yup";

import TextField from "@/components/shared/TextField";
import ColorPickerField from "@/components/shared/ColorPickerField";
import Button from "@/components/shared/Button";

import Api from "@/services/Api";

const validationSchema = yup.object().shape({
  blackListTable: yup.object().shape({
    url: yup
      .string()
      .required("–í–≤–µ–¥–∏—Ç–µ —Å—Å—ã–ª–∫—É")
      .matches(
        /\/spreadsheets\/d\/([a-zA-Z0-9-_]+)/,
        "–°—Å—ã–ª–∫–∞ –Ω–∞ —Ç–∞–±–ª–∏—Ü—É –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å –≤–∞–ª–∏–¥–Ω–æ–π"
      ),
    sheetName: yup.string().required("–í–≤–µ–¥–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ –ª–∏—Å—Ç–∞"),
    columnName: yup.string().required("–í–≤–µ–¥–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ –∫–æ–ª–æ–Ω–∫–∏"),
  }),
  checkTable: yup.object().shape({
    url: yup
      .string()
      .required("–í–≤–µ–¥–∏—Ç–µ —Å—Å—ã–ª–∫—É")
      .matches(
        /\/spreadsheets\/d\/([a-zA-Z0-9-_]+)/,
        "–°—Å—ã–ª–∫–∞ –Ω–∞ —Ç–∞–±–ª–∏—Ü—É –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å –≤–∞–ª–∏–¥–Ω–æ–π"
      ),
    sheetName: yup.string().required("–í–≤–µ–¥–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ –ª–∏—Å—Ç–∞"),
    columnName: yup.string().required("–í–≤–µ–¥–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ –∫–æ–ª–æ–Ω–∫–∏"),
  }),
  interval: yup
    .number()
    .typeError("–ù—É–∂–Ω–æ –≤–≤–µ—Å—Ç–∏ —á–∏—Å–ª–æ")
    .min(1, "–ò–Ω—Ç–µ—Ä–≤–∞–ª –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å —á–∞—â–µ —á–µ–º —Ä–∞–∑ –≤ –º–∏–Ω—É—Ç—É")
    .max(10800, "–ò–Ω—Ç–µ—Ä–≤–∞–ª –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å —Ä–µ–∂–µ —á–µ–º —Ä–∞–∑ –≤ 3 –¥–Ω—è")
    .required("–í–≤–µ–¥–∏—Ç–µ –∏–Ω—Ç–µ—Ä–≤–∞–ª –≤ –º–∏–Ω—É—Ç–∞—Ö"),
});

export default {
  components: { TextField, Button, ColorPickerField },
  props: { disabled: { type: Boolean, default: false } },

  emits: ["addPair"],
  setup(props, { emit }) {
    const { handleSubmit, isSubmitting } = useForm({ validationSchema });
    const onSubmit = handleSubmit(async (values) => {
      const newPait = await Api.createComparePair(values);
      emit("addPair", newPait);
    });

    return {
      onSubmit,
      isSubmitting,
    };
  },
};
</script>


